		"desc": "**Problema:** A empresa trabalha com compensação de horas, onde os funcionários devem trabalhar 44 horas semanais, diluídas de segunda sexta, tendo apenas 1 folga semanal (aos domingos). Aos sábados não é uma falta e nem uma folga, pois houve a compensação semanal, portanto as horas não devem ser descontadas e não deve aparecer a informação de folga ou falta para esse dia.\nNa escala atual se deixarmos o sábado sem a folga e com a jornada contratual 00:00, ele irá contabilizar como uma folga, oque está incorreto, deveria retornar um espaço em branco, e atualmente é retornado Folga.\n\n**Esperado:** Que nos casos em que a jornada diária na escala de trabalho seja igual a 00:00 no relatório de ponto, seja retornado um campo vazio ao invés de folga ou falta. ",
