		"closed": false,
		"dateLastActivity": "2021-12-15T13:02:14.655Z",
		"desc": "Problema: \nO funcionário está cadastrado em uma escala onde possui as seguinte he personalizadas:\n\nHE Domingo e Feriado: 1ª Hora - 100% (No sistema dom e folga)\nHE Segunda a Sábado: 1ª Hora - 65% (No sistema seg à sab)\n\nA escala de trabalho do funcionário é de segunda a sexta, portanto o sábado e domingo são folgas.\n\nEstá havendo uma confluência pois o sábado é folga de acordo com a escala, e na hora extra personalizada ele acaba se encaixando na he de dom e feriado.\n\nSendo assim está dando divergências nos saldos de he personalizada.\n\n(Acho que o melhor jeito de explicar é pessoalmente)\n\nEsperado:\nQue o sistema entenda a diferença entre um \"feriado\" e uma folga, pois essa confluência de folga=feriado está trazendo problemas nos cálculos. ",
