		"desc": "Problema: \nAo inserir um ponto noturno(madrugada) e fazer a movimentação do ponto o sistema \"move\" mas apresenta um erro e não atualiza o saldo do dia, quando a pagina é recarregada o ponto retorna para o inicio do apontamento e ao fazer a movimentação novamente o ponto vai sem ocorrer erro e o saldo é atualizado.\n\nEsperado:\nQue ao fazer o a movimentação de um ponto noturno o registro seja atualizado sem ocorrer erros e o saldo seja atualizado na primeira tentativa. \n\nTestei na 123 Milhas, funcionário Thales Gomide Lage (vídeo em anexo)",
