		"dateLastActivity": "2021-12-15T13:02:14.332Z",
		"desc": "Bug: Entrei no aplicativo (estava com internet), resolvi desligar o wi fi e entrei no modo offline. Ao registrar o ponto offline e ir para a tela de ''batida de Ponto Offline'' com a listagem delas, o aplicativo não permitiu que eu voltasse para uma tela anterior (fiquei preso naquela tela). \n\nFechei o aplicativo e abri novamente, e com isso o aplicativo me retornou para a tela inicial, porém ficou tudo em branco. Não conseguia registrar novos pontos offline, tampouco deslogar e verificar minhas informações. \n\nEsperado: Aplicativo deve ter algum botão para retornar a tela inicial, depois de fazer o processo do ponto offline. Além também de não ficar com tela branca quando fechar e abrir novamente. \n\nSegue vídeo elucidando todo o processo",
