		"dateLastActivity": "2021-12-15T13:02:14.178Z",
		"desc": "Necessidade da demanda: \nPreciso que replique o mesmo componente de select do banco de horas para um novo campo chamado \"Aplicar adicional noturno ao banco de horas\" (essa função já existe no back-end e existia em uma versão anterior do front).\n\nQuando o usuário clicar em ativar subirá um tooltip igual a imagem em anexo, com o seguinte o texto:\n\n\"Ao ativar essa função, você estará informando ao sistema que as horas de adicional noturno serão computadas no banco de horas dos seus funcionários, isso impactará diretamente no banco de horas dos funcionários dessa escala, tem certeza que irá ativar?\"",
