		"closed": false,
		"dateLastActivity": "2021-12-15T13:02:14.178Z",
		"desc": "Problema: Quando existe um afastamento parcial em um dia trabalho a procedure faz os calculos das horas extras e horas faltas, mas a direciona para a coluna errada. Independente do dado ser positivo ou negativo, ele é incluído na coluna Horas Extras. \n\nO problema foi encontrado em produção no banco da empresa Mult Mais Uniformes, no funcionário Pedro Emerson no dia 01/04. Foi possível replicar o mesmo comportamento em Staging no banco IN8 utilizando funcionários de teste, o que caracteriza um problema crônico.\n\nO valor deve seguir o padrão de horas trabalhadas no período do afastamento selcionado pelo usuário (expediente normal ou horas extras) e no total, caso seja positivo, deve ser incluído como hora extra, caso negativo, como hora falta.",
